* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: rgba(230, 233, 237, .58);
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

.header {
  font-size: 20px;
}

.search {
  margin-bottom: 16px;
  position: relative;
}

.search__box {
  font: inherit;
  min-width: 320px;
  border: 1px solid gray;
  border-radius: 10px;
  outline: none;
  padding: 8px 8px 8px 35px;
}

.search__box:focus {
  border: 2px solid rgba(13, 166, 213, .886);
}

.search__box:focus ~ .search__icon {
  fill: rgba(13, 166, 213, .886);
  rotate: 360deg;
}

.search__icon {
  width: 20px;
  height: 20px;
  fill: gray;
  transform-origin: center;
  transition: rotate 1s ease-in-out;
  display: block;
  position: absolute;
  top: 11px;
  left: 10px;
}

.country-list {
  max-width: 1200px;
  flex-direction: column;
  gap: 10px;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.country-list__item {
  text-align: center;
  min-width: 320px;
  max-width: 500px;
  cursor: pointer;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: -1px 11px 15px -11px rgba(66, 68, 90, .57);
}

.country-list__flag {
  width: 50px;
  cursor: pointer;
  pointer-events: none;
  border: 1px solid gray;
  margin: 10px 0;
}

.country-list__name {
  cursor: pointer;
  pointer-events: none;
  padding: 5px 20px 10px;
  font-weight: 500;
}

.country-list__heading {
  text-transform: capitalize;
  color: rgba(11, 5, 98, .435);
  cursor: none;
  pointer-events: none;
  margin: 0;
  font-size: 14px;
  font-weight: 700;
  display: block;
}

.country-list__details {
  color: #212121;
  cursor: none;
  pointer-events: none;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 16px;
  font-weight: 500;
}

.country-list__item--unfold {
  color: get-col("country-name-unfold");
  cursor: pointer;
  font-size: 18px;
  font-weight: 700;
}

.fa-solid {
  color: gray;
  pointer-events: none;
  margin-left: auto;
  margin-right: 10px;
  font-size: 16px;
  position: absolute;
  top: 10px;
  right: 10px;
}

